import React from 'react';
import { Card, Container} from 'react-bootstrap';
import { Link } from 'react-router-dom';
// import banner from '../../../Assets/sportsCategory.jpeg';
import { Swiper, SwiperSlide} from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
// import required modules
import { Pagination, Navigation,Autoplay } from "swiper";
import UseAuth from '../../Hooks/UseAuth';

const SportsCollection = () => {
    const {allProduct} = UseAuth();
    const sports =  allProduct.filter(function(product) {
        return product.category === "Sports";
      });
    return (
        <>
            <div>
            <div className='display-section'>
            <Link to="/sports">
               <h3>Sports Bra</h3>
            </Link> 
         </div>
         <div>
         <Container>
        <Swiper
            slidesPerView={4}
            spaceBetween={30}
            loop={true}
            pagination={{ clickable: true}}
            autoplay={{
                delay: 2000,
                disableOnInteraction: false,
            }}
            navigation={true}
            breakpoints={{
                240: {
                slidesPerView: 2,
                spaceBetween: 20,
                },
                600: {
                slidesPerView: 3,
                spaceBetween: 40,
                },
                800: {
                slidesPerView: 4,
                spaceBetween: 40,
                },
                1024: {
                slidesPerView: 5,
                spaceBetween: 30,
                },          
            }}
            modules={[Pagination, Navigation, Autoplay]}
            className="mySwiper"
        > 
              {sports?.map((sport, index) => (
                    <SwiperSlide key={index}>
                        <Card className='product-card'>
                            <Link to={`/details/${sport._id}`} className='order-link'>
                            <Card.Img variant="top" src={sport.img} className="img-fluid home-slide-image" />
                            <p className='home-slide-product-name'>{sport.name}</p>
                            </Link>
                        </Card>
                    </SwiperSlide>   
              ))}
        </Swiper> 
        <Link to="/sports">
          <button className='see-more'>See More</button>  
          </Link>
         </Container>
          </div>
      </div>
        </>
    );
};

export default SportsCollection;