import React from 'react';
import { Card, Container} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import UseAuth from '../../Hooks/UseAuth';
import { Swiper, SwiperSlide} from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import { Pagination, Navigation,Autoplay } from "swiper";
const NonePadedCollection = () => {
    const {allProduct} = UseAuth()
    const nonPaddeds =  allProduct.filter(function(product) {
        return product.category === "Non Padded";
      });
  
    return (
        <>
    <div>
        <div className='display-section'>
            <Link to="/Nonepadded">
               <h3>Non Padded Bra</h3>
            </Link> 
         </div>
       <div>
         <Container>
        <Swiper
            slidesPerView={4}
            spaceBetween={30}
            loop={true}
            pagination={{ clickable: true}}
            autoplay={{
                delay: 2000,
                disableOnInteraction: false,
            }}
            navigation={true}
            breakpoints={{
                240: {
                slidesPerView: 2,
                spaceBetween: 20,
                },
                600: {
                slidesPerView: 3,
                spaceBetween: 40,
                },
                800: {
                slidesPerView: 4,
                spaceBetween: 40,
                },
                1024: {
                slidesPerView: 5,
                spaceBetween: 30,
                },          
            }}
            modules={[Pagination, Navigation, Autoplay]}
            className="mySwiper"
        > 
              {nonPaddeds?.map((nonPadded, index) => (
                    <SwiperSlide key={index}>
                        <Card className='product-card'>
                            <Link to={`/details/${nonPadded._id}`} className='order-link'>
                            <Card.Img variant="top" src={nonPadded.img} className="img-fluid home-slide-image" />
                            <p className='home-slide-product-name'>{nonPadded.name}</p>
                            </Link>
                        </Card>
                    </SwiperSlide>   
              ))}
        </Swiper>  
        <Link to="/Nonepadded">
          <button className='see-more'>See More</button>  
          </Link>   
         </Container>
          </div>
      </div>
    </>
    );
};

export default NonePadedCollection;