import React from 'react';
import { Card, Container} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { Swiper, SwiperSlide} from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import { Pagination, Navigation,Autoplay } from "swiper";
import UseAuth from '../../Hooks/UseAuth';

const ExclusivePanty = () => {
    const {allProduct} = UseAuth()
    const panty =  allProduct.filter(function(product) {
        return product.category === "Panty";
      });
    //   /panty?
    return (
        <>
        <div>
            <div className='display-section'>
                <Link to=" /menstrual">
                   <h3>Comfortable Panties</h3>
                </Link> 
             </div>
           <div>
             <Container>
            <Swiper
                slidesPerView={4}
                spaceBetween={30}
                loop={true}
                pagination={{ clickable: true}}
                autoplay={{
                    delay: 2000,
                    disableOnInteraction: false,
                }}
                navigation={true}
                breakpoints={{
                    240: {
                    slidesPerView: 2,
                    spaceBetween: 20,
                    },
                    600: {
                    slidesPerView: 3,
                    spaceBetween: 40,
                    },
                    800: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                    },
                    1024: {
                    slidesPerView: 5,
                    spaceBetween: 30,
                    },          
                }}
                modules={[Pagination, Navigation, Autoplay]}
                className="mySwiper"
            > 
                  {panty?.map((panti, index) => (
                        <SwiperSlide key={index}>
                            <Card className='product-card'>
                                <Link to={`/details/${panti._id}`} className='order-link'>
                                <Card.Img variant="top" src={panti.img} className="img-fluid home-slide-image" />
                                <p className='home-slide-product-name'>{panti.name}</p>
                                </Link>
                            </Card>
                        </SwiperSlide>   
                  ))}
            </Swiper> 
            <Link to="/menstrual">
          <button className='see-more'>See More</button>  
          </Link>    
             </Container>
              </div>
          </div>
        </>
    );
};

export default ExclusivePanty;